import React from 'react';
import { Parse } from 'parse';
import { Link } from 'react-router';
import classNames from 'classnames';
import Isvg from 'react-inlinesvg';

import BookmarkButton from './BookmarkButton';

export default class StoryListItem extends React.Component {
  shouldComponentUpdate(nextProps, nextState) {
    return nextProps.objectId !== this.props.objectId;
  }

  constructor(props) {
    super(props);

    this.onClickLink = this.onClickLink.bind(this);
  }

  onClickLink(e) {
    e.preventDefault();
    if (this.props.to) {
      this.context.history.pushState(null, this.props.to, {id: this.props.objectId});
      return;
    };
    this.context.history.pushState(null, `/stories/${this.props.objectId}`);
  }

  imageContent() {
    if (this.props.image) {
      return (
          <img className='story-image' src={this.props.image._url} />
      );
    }
    return (
      <img className='story-image' src={require('../../assets/images/little_red_riding_hood.jpg')} />
    );
  }

  likesContent() {
    return (
      <span className='likes'>
        <Isvg src={require('../../assets/images/ic_favorite_black_24px.svg')} />{this.props.totalLikeCount || 0}
      </span>
    );
  }

  pagesContent() {
    return (
      <span className='pages'>
        <Isvg src={require('../../assets/images/ic_reply_black_24px.svg')} />{this.props.pageCount || 0}
      </span>
    );
  }

  render() {
    return (
      <div className='story-list-item vertical grid-block'>
        <header className='grid-content square-aspect-ratio'>
          <div className='content' onClick={this.onClickLink}>
            {this.imageContent()}
            <div className='gradient-overlay'/>
            <h1>{this.props.title}</h1>
          </div>
        </header>
        <footer className="grid-block">
          <div className="grid-block small-10">
            <BookmarkButton {...this.props} />
          </div>
          <div className="grid-block small-14 align-right">
            {this.likesContent()}
            {this.pagesContent()}
          </div>
        </footer>
      </div>
    );
  }

}

StoryListItem.contextTypes = {
  history: React.PropTypes.object
};

StoryListItem.propTypes = {
  isBookmarked: React.PropTypes.bool
};

StoryListItem.defaultProps = {
  isBookmarked: false
};



