import './assets/stylesheets/app';
import './assets/stylesheets/main';

import { Parse } from 'parse';
import React from 'react';
import { render } from 'react-dom';
import createBrowserHistory from 'history/lib/createBrowserHistory';
import { Router } from 'react-router';

import Analytics from './support/Analytics';
import secrets from '../config/secrets';
import Routes from './Routes';

Parse.initialize(secrets.parse.applicationId, secrets.parse.javascriptKey);

Analytics.init();

const app = document.createElement('div');
document.body.appendChild(app);

render(<Router routes={Routes} history={createBrowserHistory()}/>, app);



